
.zhezhao {
  position: fixed;
  height: 100%;
  width: 100%;
  background: #aaa;
  z-index: 1040;
  opacity: .5
}
.left-side {
  position: fixed;
  height: 100%;
  left: 20%;
  right: 20%;
  background: #fff;
  z-index: 1050;
  opacity: 1
}
@media only screen and (max-width: 420px) {
.left-side {
    width: 300px;
}
}
.info-header {
  height: 20%;
  background: #5bc0de;
  text-align: center;
  font-size: 52px;
}
.slide-left-enter-active {
  -webkit-animation: slide-left-in .5s ease;
          animation: slide-left-in .5s ease
}
.slide-left-leave-active {
  -webkit-animation: slide-left-out .5s ease;
          animation: slide-left-out .5s ease
}
@-webkit-keyframes slide-left-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes slide-left-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes slide-left-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes slide-left-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.show-zhezhao-leave-active {
  -webkit-animation: show-zhezhao-out .4s ease;
          animation: show-zhezhao-out .4s ease
}
@-webkit-keyframes show-zhezhao-out {
0% {
}
100% {
}
}
@keyframes show-zhezhao-out {
0% {
}
100% {
}
}



